.background-square {
    background-color: #005a4c; /* Fundo verde */
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    max-width: 600px;
    margin: 20px auto;
    box-sizing: border-box;
}

.gerenciaranalista h2 {
    margin-bottom: 10px;
    color: #fff;
    font-size: 3em; /* Alterado para unidade em para ser responsivo */
    text-align: center;
    margin-bottom: 2%;
    font-family: "Ubuntu", sans-serif;
    font-weight: bold;
}

.gerenciaranalista div {
    display: flex;
    align-items: center; /* Alinhar verticalmente */
    justify-content: space-between; /* Distribuir uniformemente os itens */
    margin-bottom: 10px;
}

.gerenciaranalista label {
    width: 150px;
    margin-right: 10px;
    text-align: right; /* Alinhamento dos rótulos */
    color: #fff;
    font-size: 16px;
    font-weight: bold;
}

.gerenciaranalista input {
    flex: 1; /* Tornar os inputs flexíveis para ocupar o espaço restante */
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
}

.matricula {
    display: flex;
    justify-content: space-between; /* Alinhar os elementos */
    align-items: center; /* Centralizar verticalmente */
}

.matricula input {
    flex: 1;
    margin-right: 10px; /* Espaço entre o input e o botão */
}

.buttons {
    display: flex;
    justify-content: center; /* Centralizar os botões horizontalmente */
}

.buttons button:first-child {
    margin-right: 20px; /* Espaçamento entre os botões */
}

.gerenciaranalista button {
    padding: 0.5em 2em; /* Alterado para unidades em para ser responsivo */
    cursor: pointer;
    border: none;
    background: linear-gradient(to left, #049a8f, #049a8f);
    color: #fff;
    border-radius: 0.5em; /* Alterado para unidades em para ser responsivo */
}

.gerenciaranalista button:hover {
    background: linear-gradient(to right, #4776E6, #049a8f);
}

.error-message {
    color: red;
    font-weight: bold;
    text-align: center; /* Centralizar a mensagem de erro */
}

@media screen and (max-width: 600px) {
    .background-square {
        margin-top: 30%; /* Adiciona espaço superior para evitar o corte pelo cabeçalho */
    }

    .gerenciaranalista div {
        flex-direction: column; /* Empilhar os itens verticalmente */
    }

    .gerenciaranalista label {
        width: auto;
        margin-right: 0;
        margin-bottom: 5px;
        text-align: left;
    }

    .matricula {
        flex-direction: column; /* Empilhar input e botão */
        align-items: flex-start;
    }

    .matricula input {
        margin-right: 0; /* Remover espaçamento adicional em telas menores */
    }

    .buttons {
        flex-direction: column; /* Empilhar os botões */
        align-items: center; /* Centralizar os botões */
    }

    .buttons button:first-child {
        margin-right: 0;
        margin-bottom: 10px;
    }
}

