.tela-Admin {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; /* Alinhar os itens no topo */
  min-height: 100vh;
  width: 100%; /* Garantir que a tela ocupe toda a largura */
}

.menu-topo {
  display: flex;
  justify-content: center; /* Alinhar os botões no centro */
  width: 100%;
  padding: 20px;
  margin-top: 150px; /* Ajuste esse valor de acordo com a altura do menu padrão */
  gap: 20px; /* Espaçamento entre os botões */
}

.menu-topo button {
  background-color: #067742; /* Verde escuro para os botões */
  color: white;
  padding: 10px 0; /* Ajuste para manter altura sem influência da largura */
  border-radius: 30px;
  border: none;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s;
  width: 150px; /* Definir uma largura fixa para os botões */
  text-align: center; /* Garantir que o texto fique centralizado */
}

.menu-topo button:hover {
  background-color: #294939; /* Verde mais escuro ao passar o mouse */
}

.conteudo-principal {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; /* Garantir que o conteúdo comece no topo */
  width: 100%; /* Ocupar 100% da largura da tela */
  padding: 20px;
  color: white;
}

.conteudo-principal h1 {
  font-size: 2rem;
  margin-bottom: 20px;
}

.conteudo-principal div {
  margin-top: 20px;
  padding: 20px;
  background-color: #005a4c; /* Cor de fundo para os blocos de conteúdo */
  border-radius: 10px;
  width: 100%; /* Ocupar 100% da largura do conteúdo principal */
  max-width: 1200px; /* Limite de largura máxima para melhorar a legibilidade em telas grandes */
  text-align: center;
}
.projeto-container {
  text-align: center;
}

.chamados-total {
  margin: 20px auto;
  display: flex;
  justify-content: center;
}

.chamados-quadrado {
  width: 200px;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f5f5f5;
  border: 2px solid #ddd;
  border-radius: 10px;
  font-size: 1.5rem;
  margin-bottom: 20px;
}

.chamados-status {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.status-quadrado {
  width: 150px;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #e0e0e0;
  border: 2px solid #ddd;
  border-radius: 10px;
  font-size: 1.2rem;
}

/* Responsividade para telas menores */
@media (max-width: 768px) {
  .menu-topo {
    flex-direction: row; /* Mantém os botões em linha */
    flex-wrap: wrap; /* Permite quebra de linha se necessário */
    justify-content: center; /* Centraliza os botões */
    gap: 15px; /* Espaçamento consistente */
    margin-top: 100px; /* Ajuste do espaçamento superior */
  }

  .menu-topo button {
    width: 120px; /* Reduz a largura dos botões */
    font-size: 14px; /* Ajusta o tamanho do texto */
    padding: 10px 5px; /* Reduz a altura do botão */
  }

  .conteudo-principal div {
    max-width: 90%; /* Reduz a largura máxima para ocupar menos espaço */
    padding: 15px; /* Ajusta o padding interno */
    font-size: 0.95rem; /* Ajusta o tamanho do texto */
    margin-bottom: 20px; /* Espaçamento entre os blocos */
  }

  .conteudo-principal h1 {
    font-size: 1.8rem; /* Reduz o tamanho do título */
    text-align: center; /* Mantém o título centralizado */
  }

  .chamados-total {
    display: flex;
    flex-wrap: wrap; /* Permite que os elementos se reorganizem */
    justify-content: center; /* Centraliza os itens */
    gap: 15px; /* Espaçamento entre os quadrados */
  }

  .chamados-quadrado {
    width: 45%; /* Reduz a largura para dois itens por linha */
    padding: 15px; /* Ajusta o espaçamento interno */
    font-size: 1rem; /* Reduz o tamanho do texto */
    border-radius: 8px; /* Mantém bordas arredondadas */
  }

  .chamados-status {
    display: flex;
    flex-wrap: wrap; /* Permite reorganização dos itens */
    justify-content: center; /* Centraliza os itens */
    gap: 10px; /* Espaçamento entre os quadrados */
  }

  .status-quadrado {
    width: 40%; /* Reduz a largura dos quadrados */
    padding: 10px; /* Ajusta o espaçamento interno */
    font-size: 0.9rem; /* Reduz o tamanho do texto */
    border-radius: 8px; /* Mantém bordas arredondadas */
  }
}

@media (max-width: 576px) {
  .menu-topo button {
    width: 100px; /* Ajusta a largura dos botões no mobile menor */
    font-size: 12px; /* Reduz o tamanho do texto */
  }

  .conteudo-principal div {
    max-width: 95%; /* Reduz ainda mais a largura máxima */
    font-size: 0.85rem; /* Ajusta o tamanho do texto */
    padding: 10px; /* Reduz o padding */
  }

  .chamados-quadrado,
  .status-quadrado {
    width: 100%; /* Cada item ocupa toda a largura no mobile menor */
    margin-bottom: 15px; /* Espaço entre os itens */
  }

  .conteudo-principal h1 {
    font-size: 1.5rem; /* Reduz ainda mais o tamanho do título */
  }
}



