@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap');

* {
    margin: 0;
    border: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;
}

.background-square {
    background-color: #005a4c; /* Fundo verde */
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    max-width: 600px;
    margin: 20px auto;
    box-sizing: border-box;
}

.telagit h2 {
    margin-bottom: 20px;
    color: #fff;
    font-size: 2em;
    text-align: center;
    font-family: 'Ubuntu', sans-serif;
    font-weight: bold;
}

.input-container {
    display: flex;
    align-items: center; /* Centraliza verticalmente os itens */
    justify-content: space-between;
    margin-bottom: 15px;
}

.input-container label {
    width: 150px; /* Mantém a largura fixa para alinhamento */
    margin-right: 10px;
    color: #fff;
    font-size: 1em;
    text-align: right; /* Alinha o texto à direita */
    font-weight: bold;
}

.input-container input {
    flex: 1; /* Ajusta para ocupar o restante do espaço */
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
}

.telagit button {
    display: block;
    padding: 0.5em 2em;
    cursor: pointer;
    margin: 20px auto 0 auto;
    border: none;
    background: linear-gradient(to left, #049a8f, #049a8f);
    color: #fff;
    border-radius: 0.5em;
    font-size: 1em;
}

.telagit button:hover {
    background: linear-gradient(to right, #4776E6, #049a8f);
}

@media screen and (max-width: 600px) {
    .background-square {
        width: 77%; /* Ajusta a largura da caixa para o mobile */
        padding: 15px;
        margin-top: 80px; /* Espaçamento superior para evitar sobreposição com o header */
    }

    .input-container {
        flex-direction: column; /* Empilha os itens verticalmente */
        align-items: flex-start;
    }

    .input-container label {
        width: auto;
        margin-right: 0;
        margin-bottom: 5px;
        text-align: left; /* Alinha os labels à esquerda no modo empilhado */
    }

    .input-container input {
        width: 94%; /* Garante que o input ocupe toda a largura disponível */
        padding: 3px 8px; /* Ajusta o padding para reduzir o tamanho do input */
        font-size: 0.9em; /* Diminui o tamanho da fonte para inputs no mobile */
    }

    .telagit h2 {
        font-size: 1.5em; /* Ajusta o tamanho do título */
    }

    .telagit button {
        padding: 0.5em 1.5em;
        font-size: 0.9em;
    }
}



